import IndexNavbar from "../../components/Navbar/IndexNavbar";
import ImageGallery from "react-image-gallery";
import "react-image-gallery/styles/css/image-gallery.css";
import { Link } from "react-router-dom";
import { useState } from "react";

// import "../../styles/gallery.css";

interface Owner {
  userId: string;
  name: string;
  address: string;
  phone: string;
}

interface Ad {
  title: string;
  price: number;
  description?: string;
  owner: Owner;
  category: string;
  brand?: string;
  model?: string;
  createdAt?: Date;
  sponsoredTill: Date;
  images: string[];
  country: string;
  features?: string[];
}

const product: Ad = {
  title: "Toyota LandCruiser",
  price: 450000,
  description: "Brand new LandCruiser from China",
  brand: "Toyota",
  model: "2020",
  owner: {
    userId: "blackJack1",
    address: "Mamponteng - Ashanti",
    name: "Black Jack",
    phone: "233596049764",
  },
  category: "Vehicles",
  sponsoredTill: new Date(),
  createdAt: new Date(),
  images: [
    "https://www.google.com/url?sa=i&url=https%3A%2F%2Fglobal.toyota%2Fen%2Fnewsroom%2Ftoyota%2F35323595.html&psig=AOvVaw2wqmH8M6-CI6EA9L0BmEI-&ust=1628244814666000&source=images&cd=vfe&ved=0CAoQjRxqFwoTCOj4gMzUmfICFQAAAAAdAAAAABAD",
    "https://images.firstpost.com/wp-content/uploads/2021/06/2022_toyota_land_cruiser_revealed_1.jpg",
    "https://gaadiwaadi.com/wp-content/uploads/2021/07/2022-Toyota-Land-Cruiser-Modellista-2-1068x601.jpg",
  ],
  country: "Ghana",
  features: [
    "V8 Twin Turbo Engine",
    "6.5L Engine",
    "4 While drive",
    "8,000 horse power",
    "650 touck",
  ],
};

const items = [
  {
    original:
      "https://global.toyota/pages/news/images/2021/06/10/0230/20210610_01_ogp.jpg",
    thumbnail: "https://via.placeholder.com/500",
  },
  {
    original:
      "https://www.topgear.com/sites/default/files/images/news-article/2021/06/a2275c897e01e22628738c8703c01a56/4_lc_300.jpg",
    thumbnail: "https://via.placeholder.com/500",
  },
  {
    original:
      "https://gaadiwaadi.com/wp-content/uploads/2021/07/2022-Toyota-Land-Cruiser-Modellista-2-1068x601.jpg",
    thumbnail: "https://via.placeholder.com/500",
  },
];

const AdDetail = () => {
  const [showContact, setShowContact] = useState(false);
  return (
    <div className="h-screen w-screen select-none overflow-x-hidden">
      <IndexNavbar />
      <div className="flex flex-col md:flex-row justify-center px-4 py-20 bg-gray-200  ">
        {/* AD DETAILS */}
        <div className=" max-w-full  md:w-3/6 bg-white  px-4 py-8">
          <ImageGallery
            items={items}
            // autoPlay={true}
            showNav={true}
            showThumbnails={false}
            showPlayButton={false}
            showBullets={false}
            showFullscreenButton={false}
            additionalClass="md:hidden"
            lazyLoad={true}
          />
          {/* DESKTOP */}
          <ImageGallery
            items={items}
            // autoPlay={true}
            showNav={false}
            thumbnailPosition="left"
            showThumbnails={true}
            showPlayButton={false}
            showBullets={false}
            showFullscreenButton={false}
            additionalClass="hidden md:block"
            lazyLoad={true}
          />
          <div
            id="product-detail-body"
            className=" flex flex-col py-6 space-y-5 px-2"
          >
            <h3 className=" text-2xl  md:text-3xl font-medium">
              {product.title}{" "}
            </h3>

            <div className="flex flex-col md:flex-row  space-y-8 md:space-y-0 justify-between md:items-start ">
              {/* price and description */}
              <div className=" flex flex-col space-y-5">
                <div id="price" className="space-y-1">
                  <span className="text-base font-bold text-orange-700">
                    Price
                  </span>{" "}
                  <h5 className="text-2xl md:text-3xl font-bold">
                    ${product.price.toLocaleString()}
                  </h5>
                </div>

                <div id="product-detail-description" className="space-y-1">
                  <span className="text-base font-bold text-orange-700">
                    Description
                  </span>
                  <p>{product.description} </p>
                </div>
              </div>
              {/* OWNER BOX */}
              <div className="flex flex-col  items-center  md:px-20">
                <Link
                  className="flex  "
                  to={`/members/${product.owner.userId}`}
                >
                  <img
                    src="https://via.placeholder.com/300"
                    className="h-[60px] w-auto rounded-full transform-gpu duration-100 hover:scale-95"
                    alt="ownerImage"
                  />
                  <h3 className=" px-2 py-4 text-black hover:text-orange-600 transform-gpu duration-150 hover:font-bold">
                    {product.owner.name}
                  </h3>
                </Link>

                <h5 className=" px-2 py-4 text-black">
                  {product.owner.address}
                </h5>
                {/* the button  */}
                {!showContact ? (
                  <button
                    className=" flex items-center space-x-2 outline-none focus:outline-none bg-orange-600 hover:text-orange-600 hover:bg-white hover:ring-2 hover:ring-orange-600 px-5 py-2 rounded-md text-sm font-bold text-white tracking-wider"
                    onClick={(e) => setShowContact(true)}
                  >
                    <span>Show Contact</span>
                    <svg
                      className="w-6 h-6"
                      fill="none"
                      stroke="currentColor"
                      viewBox="0 0 24 24"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth={2}
                        d="M15 12a3 3 0 11-6 0 3 3 0 016 0z"
                      />
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth={2}
                        d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z"
                      />
                    </svg>
                  </button>
                ) : (
                  <a
                    href={`tel:+${product.owner.phone}`}
                    target="_blank"
                    rel="noreferrer"
                    className="flex items-center space-x-2 outline-none focus:outline-none bg-orange-600 hover:text-orange-600 hover:bg-white hover:ring-2 hover:ring-orange-600 px-5 py-2 rounded-md text-sm font-bold text-white tracking-wider"
                  >
                    <svg
                      className="w-6 h-6"
                      fill="none"
                      stroke="currentColor"
                      viewBox="0 0 24 24"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth={2}
                        d="M16 3h5m0 0v5m0-5l-6 6M5 3a2 2 0 00-2 2v1c0 8.284 6.716 15 15 15h1a2 2 0 002-2v-3.28a1 1 0 00-.684-.948l-4.493-1.498a1 1 0 00-1.21.502l-1.13 2.257a11.042 11.042 0 01-5.516-5.517l2.257-1.128a1 1 0 00.502-1.21L9.228 3.683A1 1 0 008.279 3H5z"
                      />
                    </svg>

                    <span className="select-auto">{product.owner.phone}</span>
                  </a>
                )}
              </div>
            </div>

            {/* Table */}
            <div className="space-y-1 flex flex-col pt-10 md:pt-0 ">
              <span className="text-base font-bold text-orange-700">
                Specifications
              </span>
              <table className=" table-auto  border-collapse ">
                <tbody>
                  <tr>
                    <td className="border font-serif text-gray-900 md:w-2/6 border-gray-300 px-5 ">
                      Brand
                    </td>
                    <td className="border text-gray-700  border-gray-300 px-5 py-1">
                      {product.brand}
                    </td>
                  </tr>
                  <tr>
                    <td className="border font-serif text-gray-900  border-gray-300 px-5 ">
                      Date Published
                    </td>
                    <td className="border text-gray-700  border-gray-300 px-5 py-1">
                      {product.createdAt?.toDateString()}
                    </td>
                  </tr>
                  <tr>
                    <td className="border font-serif text-gray-900  border-gray-300 px-5 ">
                      Date Published
                    </td>
                    <td className="border text-gray-700  border-gray-300 px-5 py-1">
                      {product.createdAt?.toDateString()}
                    </td>
                  </tr>
                  <tr>
                    <td className="border font-serif text-gray-900  border-gray-300 px-5 ">
                      Model
                    </td>
                    <td className="border text-gray-700  border-gray-300 px-5 py-1">
                      {product.model}
                    </td>
                  </tr>
                  <tr>
                    <td className="border font-serif text-gray-900  border-gray-300 px-5 ">
                      Seller's Country
                    </td>
                    <td className="border text-gray-700  border-gray-300 px-5 py-1">
                      {product.country}
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div className="space-y-1 flex flex-col pt-10 md:pt-0 ">
              <span className="text-base font-bold text-orange-700">
                Features
              </span>
              <table className=" table-auto  border-collapse ">
                <tbody>
                  {product.features &&
                    product.features.map((fea, i) => (
                      <tr key={i}>
                        <td className="border text-gray-700 font-bold   border-gray-300 px-5 py-1">
                          {fea}
                        </td>
                      </tr>
                    ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>
        <div className="flex max-w-full p-4 mt-5 md:mt-0 md:w-3/6 md:ml-5 bg-white h-screen">
          <h3 className=" text-4xl   transform-gpu md:rotate-90">
            Related Ads
          </h3>
        </div>
        {/* RECENT ADS / SIMILAR ADS */}
      </div>
    </div>
  );
};

export default AdDetail;
